import React from "react";
import "./layout.scss";

import Header from "../Header/Header";
import Footer from "../Footer/Footer";
import FlowerTulip from "../../images/tulip.png";
import FlowerLilly from "../../images/lilly.png";

const Layout = (props) => {
  return (
    <div className="container">
      <img src={FlowerLilly} alt="" className="container__left-photo" />
      <Header />
      <main>{props.children}</main>
      <Footer />
      <img src={FlowerTulip} alt="" className="container__right-photo" />
    </div>
  );
};

export default Layout;
